import React, { Component } from "react";
import styled from "styled-components";
import Clock from "./Clock";
import axios from 'axios'
import detectEthereumProvider from "@metamask/detect-provider";
import Web3 from "web3";

const Outer = styled.div`
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  overflow: hidden;
  border-radius: 8px;
`;

class MintedNfts extends Component {

    componentDidMount() {
        const getProfile = async () => {
            const wallet = await detectEthereumProvider();
            if(wallet) {
              wallet.on('accountsChanged', function(accounts) {
                getProfile();
              })

              const web3Provider = new Web3(wallet);
      
              if(web3Provider._provider === window.ethereum) {
                const accounts = await web3Provider.eth.getAccounts();
      
                if(accounts.length > 0) {
                    this.setState({
                        ...this.state,
                        account: accounts[0]
                    })
      
                    if(accounts[0]) {
                        axios({
                            method: 'GET',
                            url: `http://46.101.207.57/account/${accounts[0]}`,
                            headers: {
                                "Content-Type": "application/json"
                            }
                        }).then((response) => {
                            if(response.status === 200) {
                                this.setState({
                                    ...this.state,
                                    profile: response.data.body
                                })
                            } else {
                                console.log(response);
                            }
                        }).catch(e => {
                            if(e.response.status === 403) {
                                this.setState({
                                    errors: e.response.data.errors
                                })
                            } else {
                                console.log(e.response)
                            }
                        })
                    }
      
                } else {
                  this.showMessage("Please connect your crypto account");
                }
      
              } else {
                this.showMessage("Please install metamask wallet.");
              }
            } else {
              this.showMessage("Please install metamask wallet.");
            }
        }
        getProfile();
    }

    showMessage = (message, time = 5000) => {
        this.setState({
            ...this.state,
            message: message,
            submitted_success: true
        })
    
        setTimeout(() => {
            this.setState({
                ...this.state,
                message: '',
                submitted_success: false
            })
        }, 5000)
    }

    constructor(props) {
        super(props);
        this.state = {
            profile: null,
            height: 0,
            message: '',
            submitted_success: false
        };
        this.onImgLoad = this.onImgLoad.bind(this);
    }

    onImgLoad({target:img}) {
        let currentHeight = this.state.height;
        if(currentHeight < img.offsetHeight) {
            this.setState({
                height: img.offsetHeight
            })
        }
    }

    likeNft = async (nft_id, e) => {
        if(e.target.style.color !== "red") {
            axios({
                method: 'GET',
                url:`http://46.101.207.57/nft/like/${this.state.account}/${nft_id}`,
                headers: {
                    "Content-Type": "application/json"
                }
            }).then(function(response) {
                if(response.status === 201 || response.status === 200) {
                    e.target.style.color = 'red'
                    document.getElementById(nft_id).innerText = parseInt(document.getElementById(nft_id).innerText) + 1;
                }
            }).catch(function(error) {
                console.log(error.response);
            })
        } else {
            axios({
                method: 'GET',
                url:`http://46.101.207.57/nft/dislike/${this.state.account}/${nft_id}`,
                headers: {
                    "Content-Type": "application/json"
                }
            }).then(function(response) {
                if(response.status === 201 || response.status === 200) {
                    e.target.style.color = 'white'
                    document.getElementById(nft_id).innerText = parseInt(document.getElementById(nft_id).innerText) - 1;
                }
            }).catch(function(error) {
                console.log(error.response);
            })
        }
    }

    render() {
        return (
            <div className='row'>
                {this.state.profile && this.state.profile.nfts.map( (nft, index) => {
                    if(nft.minted) {
                        return (
                            <div key={index} className="d-item col-lg-3 col-md-6 col-sm-6 col-xs-12">
                                <div className="nft__item">
                                    { nft.deadline &&
                                        <div className="de_countdown">
                                            <Clock deadline={nft.end_time} />
                                        </div>
                                    }
                                    <div className="nft__item_wrap" style={{height: `${this.state.height}px`}}>
                                        <Outer>
                                            <span>
                                                <img onLoad={this.onImgLoad} src={nft.content_url} className="lazy nft__item_preview" alt=""/>
                                            </span>
                                        </Outer>
                                    </div>
                                    <div className="nft__item_info">
                                        <span onClick={()=> window.open(nft._id, "_self")}>
                                            <h4>{nft.title}</h4>
                                        </span>
                                        <div className="nft__item_price">
                                            {nft.price}<span>{nft.bid}</span>
                                        </div>
                                        <div className="nft__item_action">
                                            <span onClick={()=> window.open(nft._id, "_self")}>Place a bid</span>
                                        </div>
                                        <div className="nft__item_like">
                                            <i className="fa fa-heart" onClick={(e) => this.likeNft(nft._id, e)} style={
                                                nft.likes.length > 0 && nft.likes.filter(like => {
                                                    return like.hash === this.props.account;
                                                }).length ? nft.likes.filter(like => {
                                                    return like.hash === this.props.account;
                                                })[0].hash === this.props.account  ? {color: 'red'} : {} : {}
                                            }></i>
                                            <span id={nft._id} style={{color: 'black'}}>{nft.likes.length}</span>
                                        </div>                            
                                    </div> 
                                </div>
                            </div>  
                        )
                    } else {
                        <p>No nft minted.</p>
                    }
                })}
            </div>              
        );
    }
}

export default MintedNfts;