import React, {useState} from 'react';
import Footer from '../components/footer';
import { createGlobalStyle } from 'styled-components';
import axios from 'axios';
import { Redirect } from '@reach/router';

const GlobalStyles = createGlobalStyle`
  header#myHeader.navbar.sticky.white {
    background: #403f83;
    border-bottom: solid 1px #403f83;
  }
  header#myHeader.navbar .search #quick_search{
    color: #fff;
    background: rgba(255, 255, 255, .1);
  }
  header#myHeader.navbar.white .btn, .navbar.white a, .navbar.sticky.white a{
    color: #fff;
  }
  header#myHeader .dropdown-toggle::after{
    color: rgba(255, 255, 255, .5);
  }
  header#myHeader .logo .d-block{
    display: none !important;
  }
  header#myHeader .logo .d-none{
    display: block !important;
  }
  @media only screen and (max-width: 1199px) {
    .navbar{
      background: #403f83;
    }
    .navbar .menu-line, .navbar .menu-line1, .navbar .menu-line2{
      background: #fff;
    }
    .item-dropdown .dropdown a{
      color: #fff !important;
    }
  }
`;

const Logintwo = ({auth, setAuth}) => {
  const [loginData, setloginData] = useState({ username: '', password: ''});
  const [errors, setErrors] = useState({ username: [], password: [] });

  const onChangeInput = (e) => {
    setloginData({
      ...loginData,
      [e.target.name]: e.target.value
    })
  }

  const onSubmit = (e) => {
    e.preventDefault();
    axios({
      method: 'POST',
      url: 'http://46.101.207.57/users/',
      headers:{
        "Content-Type": "application/json"
      },
      data: {
        username: loginData.username,
        password: loginData.password
      }
    }).then(response => {
      if(response.status === 200 && response.data.success === true) {
        if(response.data.body._token !== undefined) {
          if(response.data.body._token !== "") {
            localStorage.setItem('_token', response.data.body._token);
            setAuth(localStorage.getItem('_token') ? true : false);
            setloginData({
              username: '',
              password: ''
            })
          }
        }
      }
    }).catch(e => {
      setAuth(localStorage.getItem('_token') ? true : false);
      if(e.response.status === 401 || e.response.status === 403) {
        if(e.response.data.errors !== undefined) {
          console.log(e.response.data.errors)
          setErrors({
            ...(e.response.data.errors)
          })
        }
      }
    })
  }

  return (
    <div>
      
      {
        auth ? (<Redirect to='/home' noThrow/>) : (
          ''
        )
      }
      <GlobalStyles/>
      
      <section className='jumbotron breadcumb no-bg' style={{backgroundImage: `url(${'./img/background/subheader.jpg'})`}}>
        <div className='mainbreadcumb'>
          <div className='container'>
            <div className='row align-items-center'>
              <div className="col-lg-5 text-light wow fadeInRight" data-wow-delay=".5s">
                  <div className="spacer-10"></div>
                  <h1>Create, sell or collect digital items.</h1>
                  <p className="lead">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua ut enim.</p>
              </div>
              <div className="col-lg-4 offset-lg-2 wow fadeIn" data-wow-delay=".5s">
                <div className="box-login">
                  <h3 className="mb10">Sign In</h3>
                  <p>Login using an existing account or create a new account <span>here</span>.</p>
                  <form name="contactForm" id='contact_form' className="form-border" action='#' method='post' onSubmit={onSubmit}>
    
                      <div className="field-set">
                          <input type='text' name='username' id='username' value={loginData.username}  className="form-control" placeholder="username" onChange={onChangeInput}/>
                          <span>{errors.username[0]}</span>
                      </div>
                    
                      <div className="field-set">
                          <input type='password' name='password' value={loginData.password}  id='password' className="form-control" placeholder="password" onChange={onChangeInput}/>
                          <span>{errors.password[0]}</span>
                      </div>
                    
                    <div className="field-set">
                      <input type='submit' id='send_message' value='Submit' className="btn btn-main btn-fullwidth color-2" />
                    </div>
                    
                    <div className="clearfix"></div>
                    
                    <div className="spacer-single"></div>
                      <ul className="list s3">
                          <li>Login with:</li>
                          <li><span >Facebook</span></li>
                          <li><span >Google</span></li>
                      </ul>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <Footer />
    </div>
  );
};
export default Logintwo;