import detectEthereumProvider from "@metamask/detect-provider";
import axios from "axios";
import React, { Component } from "react";
import styled from "styled-components";
import Web3 from "web3";
import Clock from "./Clock";
import WPNFTS from '../../abis/WPNFTs.json';
import {Link} from '@reach/router'

const Outer = styled.div`
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  overflow: hidden;
  border-radius: 8px;
`;



class Responsive extends Component {

    NavLink = props => (
        <Link 
          {...props}
          getProps={({ isCurrent }) => {
            // the object returned here is passed to the
            // anchor element's props
            return {
              className: isCurrent ? 'active' : 'non-active',
            };
          }}
        />
    );

    componentDidMount = async () => {
        const wallet = await detectEthereumProvider();
        if(wallet) {
            await this.loadNftitems(wallet);
            wallet.on('accountsChanged', async () => {
                await this.loadNftitems(wallet)
            });
        } else {
            this.showMessage("Please install metamask wallet.", 5000);
        }

        var app = this;
        wallet.on('accountsChanged', function(accounts) {
            if(accounts.length < 1) {
                app.showMessage("Connect your Crypto wallet.")
            }
            console.log('account has been changed.')
            app.setState({
                ...app.state,
                account: accounts[0]
            })
        })
    }

    loadNftitems = async (wallet) => {
        const web3Provider = new Web3(wallet);

        if(web3Provider) {
            this.setState({
                ...this.state,
                web3Provider: web3Provider
            })

            const accounts = await web3Provider.eth.getAccounts();
            if(accounts.length > 0) {
                this.setState({
                    ...this.state,
                    account: accounts[0]
                })
                axios({
                    method: 'GET',
                    url: `http://46.101.207.57/nft/${accounts[0]}`,
                    headers: {
                        "Content-Type": "application/json"
                    }
                }).then(response => {
                    if(response.status === 200 && response.data.success) {
                        this.setState({
                            ...this.state,
                            nfts: response.data.body
                        })
                        console.log(response.data);
                    } else {
                        console.log(response.status, response.data);
                    }
                    console.log(response);
                }).catch(e => {
                    console.log(e.response);
                })
            } else {
                this.showMessage("Please connect your crypto account to the site.");
            }
        } else {
            this.showMessage("Please connect your crypto account to the site.");
        }
    }

    showMessage = (message, time=5000) => {
        this.setState({
          ...this.state,
          message: message,
          submitted_success: true
        })
    
        setTimeout(() => {
          this.setState({
            ...this.state,
            submitted_success: false
          })
        }, 5000)
    }

    constructor(props) {
        super(props);
        this.state = {
            submitted_success: false,
            message: '',
            nfts: null,
            web3Provider: null,
            account: null,
            totalSupply: 0
        }
        this.onImgLoad = this.onImgLoad.bind(this);
    }

    loadMore = () => {
        let nftState = this.state.nfts
        let start = nftState.length
        let end = nftState.length+4
        this.setState({
            nfts: [...nftState, ...(this.dummyData.slice(start, end))]
        });
    }

    onImgLoad({target:img}) {
        let currentHeight = this.state.height;
        if(currentHeight < img.offsetHeight) {
            this.setState({
                height: img.offsetHeight
            })
        }
    }

    likeNft = async (nft_id, e) => {
        if(e.target.style.color !== "red") {
            axios({
                method: 'GET',
                url:`http://46.101.207.57/nft/like/${this.state.account}/${nft_id}`,
                headers: {
                    "Content-Type": "application/json"
                }
            }).then(function(response) {
                if(response.status === 201 || response.status === 200) {
                    e.target.style.color = 'red'
                    document.getElementById(nft_id).innerText = parseInt(document.getElementById(nft_id).innerText) + 1;
                }
            }).catch(function(error) {
                console.log(error.response);
            })
        } else {
            axios({
                method: 'GET',
                url:`http://46.101.207.57/nft/dislike/${this.state.account}/${nft_id}`,
                headers: {
                    "Content-Type": "application/json"
                }
            }).then(function(response) {
                if(response.status === 201 || response.status === 200) {
                    e.target.style.color = 'white'
                    document.getElementById(nft_id).innerText = parseInt(document.getElementById(nft_id).innerText) - 1;
                }
            }).catch(function(error) {
                console.log(error.response);
            })
        }
    }

    mintToken = async () => {
        if(this.state.web3Provider) {
            const web3 = this.state.web3Provider;
            const networkId = await web3.eth.net.getId();
            const networkInterface = WPNFTS['networks'][networkId];

            if(networkInterface) {
                const address = networkInterface['address'];
                const contract = new web3.eth.Contract(WPNFTS.abi, address);
                const app_ref = this;
                contract.methods.totalSupply().call({from: this.state.account}, function(error, result) {
                    if(error) {
                        window.location.href = 'http://localhost:3000/owned-nfts-item';
                    } else {
                        contract.methods.mint(app_ref.state.account, app_ref.state.totalSupply).send({
                            from: app_ref.state.account
                        }, function(error, result) {
                            if(error) {
                                console.log('found error : ' + error);
                            } else {
                                console.log('minted successfully : ' + result);
                            }
                        });
                    }
                });
            } else {
                this.showMessage("Testing Network is allowed.")
            }
        } else {
            this.showMessage("Please install metamask wallet and connect it.");
        }
    }

    render() {
        return (
            <div className='row'>
                {this.state.submitted_success ? 
                    (<div className="alert alert-success" role="alert">
                        {this.state.message}
                    </div>) : (<p></p>)
                }
                {this.state.nfts && this.state.nfts.nfts.map( (nft, index) => (
                    <div key={index} className="d-item col-lg-3 col-md-6 col-sm-6 col-xs-12 mb-4">
                        <div className="nft__item m-0">
                            { nft.deadline &&
                                <div className="de_countdown">
                                    <Clock deadline={nft.end_date} />
                                </div>
                            }
                            <div className="author_list_pp">
                                <this.NavLink to="/my-profile">
                                    <img className="lazy" src={this.state.nfts.profile_pic} alt=""/>
                                    <i className="fa fa-check"></i>
                                </this.NavLink>
                            </div>
                            <div className="nft__item_wrap" style={{height: `${this.state.height}px`}}>
                            <Outer>
                                <span>
                                    <img onLoad={this.onImgLoad} src={nft.content_url} className="lazy nft__item_preview" alt=""/>
                                </span>
                            </Outer>
                            </div>
                            <div className="nft__item_info">
                                <this.NavLink to={`/single-nft-item/${nft._id}`}>
                                    <h4>{nft.title}</h4>
                                </this.NavLink>
                                <span onClick={()=> window.open(nft.content_url, "_self")}>
                                    
                                </span>
                                <div className="nft__item_price">
                                    {nft.price}<span>1 / {this.state.nfts.nfts.length}</span>
                                </div>
                                <div className="nft__item_action">
                                    <span onClick={()=> window.open(nft._id, "_self")}>Place a bid</span>
                                </div>
                                <div className="nft__item_like">
                                <i className="fa fa-heart" onClick={(e) => this.likeNft(nft._id, e)} style={
                                    nft.likes.length > 0 && nft.likes.filter(like => {
                                        return like.hash === this.state.account;
                                    }).length ? nft.likes.filter(like => {
                                        return like.hash === this.state.account;
                                    })[0].hash === this.state.account  ? {color: 'red'} : {} : {}
                                }></i>
                                <span id={nft._id} style={{color: 'black'}}>{nft.likes.length}</span>
                                </div>                            
                            </div> 
                            <div className="flex-d">
                                <button className="button-mint" onClick={this.mintToken}>Mint NFT</button>
                                <button className="button-edit">Edit Item</button>
                            </div>
                        </div>
                    </div>  
                ))}
                {/* { this.state.nfts.length !== this.dummyData.length &&
                    <div className='col-lg-12'>
                        <div className="spacer-single"></div>
                        <span onClick={() => this.loadMore()} className="btn-main lead m-auto">Load More</span>
                    </div>
                } */}
            </div>              
        );
    }
}

export default Responsive