import React from 'react';
import { Router, Location, Redirect } from '@reach/router';
import ScrollToTopBtn from './menu/ScrollToTop';
import Header from './menu/header';
import Home from './pages/home';
import Home1 from './pages/home1';
import Home2 from './pages/home2';
import Home3 from './pages/home3';
import Explore from './pages/explore';
import Explore2 from './pages/explore2';
import Rangking from './pages/rangking';
import Auction from './pages/Auction';
import Helpcenter from './pages/helpcenter';
import Colection from './pages/colection';
import NftItem from './pages/NftItem';
import Author from './pages/Author';
import Wallet from './pages/wallet';
import Login from './pages/login';
import LoginTwo from './pages/loginTwo';
import Register from './pages/register';
import Price from './pages/price';
import Works from './pages/works';
import News from './pages/news';
import MyProfile from './pages/MyProfile';
import OtherProfile from './pages/OtherProfile';
import UpdateProfile from './pages/UpdateProfile';
import Create from './pages/create';
import CreateNftItem from './pages/CreateNftItem';
import Create2 from './pages/create2';
import Create3 from './pages/create3';
import Createoption from './pages/createOptions';
import Activity from './pages/activity';
import Contact from './pages/contact';
import ElegantIcons from './pages/elegantIcons';
import EtlineIcons from './pages/etlineIcons';
import FontAwesomeIcons from './pages/fontAwesomeIcons';
import Accordion from './pages/accordion';
import Alerts from './pages/alerts';
import Progressbar from './pages/progressbar';
import Tabs from './pages/tabs';

import { createGlobalStyle } from 'styled-components';
import ExploreNfts from './pages/ExploreNfts';
import ExploreOwnedNfts from './pages/ExploreOwnedNfts';

const GlobalStyles = createGlobalStyle`
  :root {
    scroll-behavior: unset;
  }
`;

export const ScrollTop = ({ children, location }) => {
  React.useEffect(() => window.scrollTo(0,0), [location])
  return children
}

const PosedRouter = ({ children }) => (
  <Location>
    {({ location }) => (
      <div id='routerhang'>
        <div key={location.key}>
          <Router location={location}>
            {children}
          </Router>
        </div>
      </div>
    )}
  </Location>
);

const App = () => {
  const [account, setAccount] = React.useState(null);
  const [web3Provider, setWeb3Provider] = React.useState(null);
  const [networkId, setNetworkId] = React.useState(null);
  const [balance, setBalance] = React.useState(null);

  return (
  <div className="wraper">
    <GlobalStyles />
    <Header 
      account={account}
      setAccount={setAccount}
      web3Provider={web3Provider}
      setWeb3Provider={setWeb3Provider}
      networkId={networkId}
      setNetworkId={setNetworkId}
      balance={balance}
      setBalance={setBalance}
    />
    <PosedRouter>
      <ScrollTop path="/">
        <Home exact path="/">
          <Redirect to="/home" />
        </Home>
        <Home1 path="/home1" />
        <Home2 path="/home2" />
        <Home3 path="/home3" />
        <Explore path="/explore" />
        <ExploreNfts path="/explore-nfts" />
        <ExploreOwnedNfts path="/owned-nfts-item" account={account} />
        <Explore2 path="/explore2" />
        <Rangking path="/rangking" />
        <Auction path="/Auction" />
        <Helpcenter path="/helpcenter" />
        <Colection path="/colection" />
        <NftItem path="/single-nft-item/:nft_id" />
        <Author path="/Author" />
        <Wallet path="/wallet" 
          account={account}
          setAccount={setAccount}
          web3Provider={web3Provider}
          setWeb3Provider={setWeb3Provider}
          networkId={networkId}
          setNetworkId={setNetworkId}
          balance={balance}
          setBalance={setBalance}
        />
        <Login path="/login"/>
        <LoginTwo path="/loginTwo" />
        <Register path="/register"/>
        <Price path="/price" />
        <Works path="/works" />
        <News path="/news" />
        <MyProfile path="/my-profile" 
          account={account}
          setAccount={setAccount}
          web3Provider={web3Provider}
          setWeb3Provider={setWeb3Provider}
          networkId={networkId}
          setNetworkId={setNetworkId}
          balance={balance}
          setBalance={setBalance}
        />
        <OtherProfile path="/other-profile/:nft_id" />
        <UpdateProfile path="/updateProfile" account={account} />
        <CreateNftItem path="/create-nft-item" />
        <Create path="/create" />
        <Create2 path="/create2" />
        <Create3 path="/create3" />
        <Createoption path="/createOptions" />
        <Activity path="/activity" />
        <Contact path="/contact" />
        <ElegantIcons path="/elegantIcons" />
        <EtlineIcons path="/etlineIcons" />
        <FontAwesomeIcons path="/fontAwesomeIcons" />
        <Accordion path="/accordion" />
        <Alerts path="/alerts" />
        <Progressbar path="/progressbar" />
        <Tabs path="/tabs" />
      </ScrollTop>
    </PosedRouter>
    <ScrollToTopBtn />
  </div>
  );
};
export default App;