import detectEthereumProvider from "@metamask/detect-provider";
import axios from "axios";
import React, { Component } from "react";
import styled from "styled-components";
import Web3 from "web3";
import Clock from "./Clock";
import {Link} from '@reach/router';

const Outer = styled.div`
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  overflow: hidden;
  border-radius: 8px;
`;

class ColumnNft extends Component {

    NavLink = props => (
        <Link 
          {...props}
          getProps={({ isCurrent }) => {
            // the object returned here is passed to the
            // anchor element's props
            return {
              className: isCurrent ? 'active' : 'non-active',
            };
          }}
        />
    );

    componentDidMount = async () => {
        const wallet = await detectEthereumProvider();
        if(wallet) {
            const web3Provider = new Web3(wallet);
            if(web3Provider._provider === window.ethereum) {
                const accounts = await web3Provider.eth.getAccounts();
                if(accounts.length > 0) {
                    this.setState({
                        ...this.state,
                        account: accounts[0]
                    })
                }
            }
            var app = this;
            wallet.on('accountsChanged', function(accounts) {
                console.log('account has been changed.')
                console.log(this.state);
                app.setState({
                    ...app.state,
                    account: accounts[0]
                })
            })
        }

        axios({
            method: 'GET',
            url: 'http://46.101.207.57/nft/',
            headers: {
                "Content-Type": "application/json"
            },
            params: {
                filter: this.props.filter ? this.props.filter : ''
            }
        }).then(response => {
            if(response.status === 200 && response.data.success) {
                this.setState({
                    ...this.state,
                    nfts: response.data.body
                })
            } else {
                console.log(response.status, response.data);
            }
        }).catch(e => {
            console.log(e.response);
        })
    }

    constructor(props) {
        super(props);
        this.state = {
            nfts: null,
            account: null,
            liked: false
        }
        this.onImgLoad = this.onImgLoad.bind(this);
    }

    loadMore = () => {
        let nftState = this.state.nfts
        let start = nftState.length
        let end = nftState.length+4
        this.setState({
            nfts: [...nftState, ...(this.dummyData.slice(start, end))]
        });
    }

    onImgLoad({target:img}) {
        let currentHeight = this.state.height;
        if(currentHeight < img.offsetHeight) {
            this.setState({
                height: img.offsetHeight
            })
        }
    }

    likeNft = async (nft_id, e) => {
        console.log(nft_id)
        if(e.target.style.color !== "red") {
            axios({
                method: 'GET',
                url:`http://46.101.207.57/nft/like/${this.state.account}/${nft_id}`,
                headers: {
                    "Content-Type": "application/json"
                }
            }).then(function(response) {
                if(response.status === 201 || response.status === 200) {
                    e.target.style.color = 'red'
                    document.getElementById(nft_id).innerText = parseInt(document.getElementById(nft_id).innerText) + 1;
                }
            }).catch(function(error) {
                console.log(error.response);
            })
        } else {
            axios({
                method: 'GET',
                url:`http://46.101.207.57/nft/dislike/${this.state.account}/${nft_id}`,
                headers: {
                    "Content-Type": "application/json"
                }
            }).then(function(response) {
                if(response.status === 201 || response.status === 200) {
                    e.target.style.color = 'white'
                    document.getElementById(nft_id).innerText = parseInt(document.getElementById(nft_id).innerText) - 1;
                }
            }).catch(function(error) {
                console.log(error.response);
            })
        }
    }

 render() {
  return (
    <div className='row'>
        {this.state.nfts && this.state.nfts.map( (nft, index) => (
            nft.nfts.length > 0 ? <div key={index} className="d-item col-lg-3 col-md-6 col-sm-6 col-xs-12 mb-4">
            <div className="nft__item m-0">
                { nft.deadline &&
                    <div className="de_countdown">
                        <Clock deadline={nft.nfts[nft.nfts.length - 1].end_date} />
                    </div>
                }
                <div className="author_list_pp">
                    <this.NavLink to={`/other-profile/${nft.hash}`}>
                        <span style={{height: '50px', objectFit: 'cover'}}>
                            <img className="lazy" src={nft.profile_pic} alt="" style={{height: '50px', objectFit: 'cover'}}/>
                            <i className="fa fa-check"></i>
                        </span>
                    </this.NavLink>
                </div>
                <div className="nft__item_wrap" style={{height: `${this.state.height}px`}}>
                  <Outer>
                    <span>
                        {nft.nfts[nft.nfts.length - 1].category !== 'music' ? (<img onLoad={this.onImgLoad} src={nft.nfts[nft.nfts.length - 1].content_url} className="lazy nft__item_preview" alt=""/>) : (<audio controls="controls" id="audio_player" preload="metadata">
                                                            <source src={nft.nfts[nft.nfts.length - 1].content_url} type="audio/mp3"></source>
                                                        </audio>)}
                        
                    </span>
                  </Outer>
                </div>
                <div className="nft__item_info">
                    <span onClick={()=> window.open(nft.nftLink, "_self")}>
                        <h4>{nft.nfts[nft.nfts.length - 1].title}</h4>
                    </span>
                    <div className="nft__item_price">
                        {nft.nfts[nft.nfts.length - 1].price}<span>1 / {nft.nfts.length}</span>
                    </div>
                    <div className="nft__item_action">
                        <span onClick={() => window.open(nft.nfts[nft.nfts.length - 1]._id, "_self")}>Place a bid</span>
                    </div>
                    <div className="nft__item_like">
                        <i className="fa fa-heart" onClick={(e) => this.likeNft(nft.nfts[nft.nfts.length - 1]._id, e)} style={
                            nft.nfts[nft.nfts.length - 1].likes.length > 0 && nft.nfts[nft.nfts.length - 1].likes.filter(like => {
                                return like.hash === this.state.account;
                            }).length ? nft.nfts[nft.nfts.length - 1].likes.filter(like => {
                                return like.hash === this.state.account;
                            })[0].hash === this.state.account  ? {color: 'red'} : {} : {}
                        }></i>
                        <span style={{color: 'black'}} id={nft.nfts[nft.nfts.length - 1]._id}>{nft.nfts[nft.nfts.length - 1].likes ? nft.nfts[nft.nfts.length - 1].likes.length : 0}</span>
                    </div>
                </div> 
            </div>
        </div> : ''  
        ))}
        {/* { this.state.nfts.length !== this.dummyData.length &&
            <div className='col-lg-12'>
                <div className="spacer-single"></div>
                <span onClick={() => this.loadMore()} className="btn-main lead m-auto">Load More</span>
            </div>
        } */}
    </div>              
    );
}
}

export default ColumnNft