import React, { useRef, useEffect, useState } from 'react';
import Web3 from 'web3';
import Web3Modal from 'web3modal';
import { providers } from 'ethers';
import WalletConnectProvider from "@walletconnect/web3-provider";
import CoinbaseWalletSDK from '@coinbase/wallet-sdk';
import { Bitski } from "bitski";
import Fortmatic from "fortmatic";
import Torus from "@toruslabs/torus-embed";


const Wallet = ({account, setAccount, web3Provider, setWeb3Provider, networkId, setNetworkId, balance, setBalance}) => {
    const [connectedWallet, setConnectedWallet] = useState(false);
    const [providerOptions, setProviderOptions] = useState({});
    const web3ModalRef = useRef();

    useEffect(() => {
        web3ModalRef.current = new Web3Modal({
          network: "rinkeby",
          providerOptions: providerOptions,
        });
    }, []);

    const getSignerOrProvider = async (walletType='metamask', needSigner = false) => {
        const providerOptions = {
            walletconnect: {
                package: WalletConnectProvider, // required
                options: {
                  infuraId: "57337e5b60134b7ab5ef3b53f4a6607e" // required
                }
            },
            coinbasewallet: {
                package: CoinbaseWalletSDK,
                options: {
                    ppName: "55 Dollers", // Required
                    infuraId: "57337e5b60134b7ab5ef3b53f4a6607e", // Required
                    chainId: 4, // Optional. It defaults to 1 if not provided
                    darkMode: false
                }
            },
            bitski: {
                package: Bitski, // required
                options: {
                  clientId: "51c62c9a-a432-4f39-b831-0d382d0b4b82", // required
                  callbackUrl: "http://localhost:3000/wallet" // required
                }
            },
            fortmatic: {
                package: Fortmatic, // required
                options: {
                  key: "pk_test_FACCC1876B24570D", // required
                  network: {
                    rpcUrl: 'https://rpc-mumbai.maticvigil.com/v1/923def4b1633bb51664d79d9173bbdc0faaca97a',
                    chainId: 80001
                  } // if we don't pass it, it will default to localhost:8454
                }
            },
            torus: {
                package: Torus, // required
                options: {
                    networkParams: {
                        host: 'HTTP://127.0.0.1:7545',
                        chainId: 1337,
                        networkId: 5777
                    },
                    config: {
                        buildEnv: "development" // optional
                    }
                }
            }         
        };

        if(walletType === 'metamask') {
            const provider = await web3ModalRef.current.connect();
            const Web3Provider = new providers.Web3Provider(provider);

            const { chainId } = await Web3Provider.getNetwork();

            if (chainId !== 4) {
                alert("USE RINKEEBY NETWORK");
                throw new Error("Change network to Rinkeby");
            }
            if (needSigner) {
                const signer = Web3Provider.getSigner();
                return signer;
            }
            return provider;
        } else if(walletType === 'coinbase') {
            const web3Model = new Web3Modal({
                network: 'rinkeby',
                providerOptions: providerOptions
            });
            const provider = await web3Model.connectTo('coinbasewallet');
            return provider;
        } else if (walletType === 'walletconnect') {
            const web3Model = new Web3Modal({
                network: 'rinkeby',
                providerOptions: providerOptions
            });
            const provider = await web3Model.connectTo('walletconnect');
            return provider;
        } else if(walletType === 'bitski') {
            const web3Model = new Web3Modal({
                network: 'rinkeby',
                providerOptions: providerOptions
            });
            const provider = await web3Model.connectTo('bitski');
            return provider;
        } else if(walletType === 'fortmatic') {
            const web3Model = new Web3Modal({
                network: 'rinkeby',
                providerOptions: providerOptions
            });
            var fortMaticConnect = await web3Model.connectTo('fortmatic');
            const fm = new Fortmatic(fortMaticConnect.apiKey);
            return fm.getProvider();
        } else if(walletType === 'torus') {
            // const web3Model = new Web3Modal({
            //     network: 'rinkeby',
            //     providerOptions: providerOptions
            // });
            // const provider = await web3Model.connectTo('torus');
            // console.log(provider);
            const torus = new Torus();
            await torus.init();
            await torus.login(); // await torus.ethereum.enable()
            const provider = torus.provider;
            return provider;
        } else {
            console.log('No wallet type provided.');
        }
    };

    const connectWallet = async (walletType) => {
        if (walletType !== '') {
            try {
                const provider = await getSignerOrProvider(walletType);
                setConnectedWallet(true);

                const web3 = new Web3(provider);
                const accounts = await web3.eth.getAccounts();
                const network = await web3.eth.net.getId();
                if(accounts.length > 0) {
                    var account_balance = await web3.eth.getBalance(accounts[0]);
                    account_balance = web3.utils.fromWei(account_balance.toString(), 'ether');
                    setBalance(parseFloat(account_balance))
                }

                if(accounts.length > 0) setAccount(accounts[0]);
                if(network) setNetworkId(network);
                console.log(provider);
            } catch(error) {
                console.log('error + ' + error.message);
            }
        }
        else {
            console.log(`There is no configuration for ${walletType}.`);
        }
    }

    return (
        <div className="row">
          <div className="col-lg-3 mb30" onClick={async () => connectWallet('metamask')}>
              <span className="box-url">
                  <span className="box-url-label">Most Popular</span>
                  <img src="./img/wallet/1.png" alt="" className="mb20"/>
                  <h4>Metamask</h4>
                  <p>Start exploring blockchain applications in seconds.  Trusted by over 1 million users worldwide.</p>
              </span>
          </div>
      
          <div className="col-lg-3 mb30" onClick={async () => connectWallet('bitski')}>
              <span className="box-url">
                  <img src="./img/wallet/2.png" alt="" className="mb20"/>
                  <h4>Bitski</h4>
                  <p>Bitski connects communities, creators and brands through unique, ownable digital content.</p>
              </span>
          </div>       
      
          <div className="col-lg-3 mb30" onClick={async () => connectWallet('fortmatic')}>
              <span className="box-url">
                  <img src="./img/wallet/3.png" alt="" className="mb20"/>
                  <h4>Fortmatic</h4>
                  <p>Let users access your Ethereum app from anywhere. No more browser extensions.</p>
              </span>
          </div>    
      
          <div className="col-lg-3 mb30" onClick={async () => connectWallet('walletconnect')}>
              <span className="box-url">
                  <img src="./img/wallet/4.png" alt="" className="mb20"/>
                  <h4>WalletConnect</h4>
                  <p>Open source protocol for connecting decentralised applications to mobile wallets.</p>
              </span>
          </div>
      
          <div className="col-lg-3 mb30" onClick={async () => connectWallet('coinbase')}>
              <span className="box-url">
                  <img src="./img/wallet/5.png" alt="" className="mb20"/>
                  <h4>Coinbase Wallet</h4>
                  <p>The easiest and most secure crypto wallet. ... No Coinbase account required.
                  </p>
              </span>
          </div>
      
          <div className="col-lg-3 mb30">
              <span className="box-url">
                  <img src="./img/wallet/6.png" alt="" className="mb20"/>
                  <h4>Arkane</h4>
                  <p>Make it easy to create blockchain applications with secure wallets solutions.</p>
              </span>
          </div>       
      
          <div className="col-lg-3 mb30">
              <span className="box-url">
                  <img src="./img/wallet/7.png" alt="" className="mb20"/>
                  <h4>Authereum</h4>
                  <p>Your wallet where you want it. Log into your favorite dapps with Authereum.</p>
              </span>
          </div>    
      
          <div className="col-lg-3 mb30" onClick={async () => connectWallet('torus')}>
              <span className="box-url">
                  <span className="box-url-label">Most Simple</span>
                  <img src="./img/wallet/8.png" alt="" className="mb20"/>
                  <h4>Torus</h4>
                  <p>Open source protocol for connecting decentralised applications to mobile wallets.</p>
              </span>
          </div>                                  
      </div>
    )
};
export default Wallet;