import React from "react";
import ColumnZero from '../components/ColumnZero';
import ColumnZeroTwo from '../components/ColumnZeroTwo';
import ColumnZeroThree from '../components/ColumnZeroThree';
import Footer from '../components/footer';
import { createGlobalStyle } from 'styled-components';
import axios from "axios";
import detectEthereumProvider from "@metamask/detect-provider";
import Web3 from "web3";

const GlobalStyles = createGlobalStyle`
  header#myHeader.navbar.white {
    background: #fff;
  }
  @media only screen and (max-width: 1199px) {
    .navbar{
      background: #403f83;
    }
    .navbar .menu-line, .navbar .menu-line1, .navbar .menu-line2{
      background: #111;
    }
    .item-dropdown .dropdown a{
      color: #111 !important;
    }
  }
`;

const Colection = () => {
    const [openMenu, setOpenMenu] = React.useState(true);
    const [openMenu1, setOpenMenu1] = React.useState(false);
    const [openMenu2, setOpenMenu2] = React.useState(false);
    const [message, setMessage] = React.useState('');
    const [submitted_success, setSubmitted_success] = React.useState(false);
    const [account, setAccount] = React.useState(null);
    const [profile, setProfile] = React.useState({});
    const [errors, setErrors] = React.useState({});

    React.useEffect(() => {
      const getProfile = async () => {
        const wallet = await detectEthereumProvider();
        if(wallet) {
          const web3Provider = new Web3(wallet);
  
          if(web3Provider._provider === window.ethereum) {
            const accounts = await web3Provider.eth.getAccounts();
  
            if(accounts.length > 0) {
              setAccount(accounts[0]);
  
              if(accounts[0]) {
                axios({
                  method: 'GET',
                  url: `http://46.101.207.57/account/${accounts[0]}`,
                  headers: {
                    "Content-Type": "application/json"
                  }
                }).then((response) => {
                  if(response.status === 200) {
                    setProfile(response.data.body);
                  } else {
                    console.log(response);
                  }
                }).catch(e => {
                  if(e.response.status === 403) {
                    setErrors(e.response.data.errors);
                  } else {
                    console.log(e.response)
                  }
                })
              }
  
            } else {
              showMessage("Please connect your crypto account");
            }
  
          } else {
            showMessage("Please install metamask wallet.");
          }
        } else {
          showMessage("Please install metamask wallet.");
        }
      }
      getProfile();
    }, [])

    

    const showMessage = (message, time=5000) => {
      setMessage(message);
      setSubmitted_success(true);
  
      setTimeout(() => {
        setMessage('');
        setSubmitted_success(false);
      }, 5000)
    }

    const handleBtnClick = () => {
      setOpenMenu(!openMenu);
      setOpenMenu1(false);
      setOpenMenu2(false);
      document.getElementById("Mainbtn").classList.add("active");
      document.getElementById("Mainbtn1").classList.remove("active");
      document.getElementById("Mainbtn2").classList.remove("active");
    };
    const handleBtnClick1 = () => {
      setOpenMenu1(!openMenu1);
      setOpenMenu2(false);
      setOpenMenu(false);
      document.getElementById("Mainbtn1").classList.add("active");
      document.getElementById("Mainbtn").classList.remove("active");
      document.getElementById("Mainbtn2").classList.remove("active");
    };
    const handleBtnClick2 = () => {
      setOpenMenu2(!openMenu2);
      setOpenMenu(false);
      setOpenMenu1(false);
      document.getElementById("Mainbtn2").classList.add("active");
      document.getElementById("Mainbtn").classList.remove("active");
      document.getElementById("Mainbtn1").classList.remove("active");
    };

    const formatWalletAddress = (address) => {
      return address !== ""
        ? address.substring(0, 5) +
            "..." +
            address.substring(address.length - 4, address.length)
        : "";
    };

    const copyToClipboard = () => {
      var copyText = document.getElementById("wallet");
    
       /* Copy the text inside the text field */
      navigator.clipboard.writeText(copyText.attributes["data-id"].value);
    } 



    return (
      <div>
        <GlobalStyles />
        <section id='profile_banner' className='jumbotron breadcumb no-bg' style={{backgroundImage: `url(${'./img/author_single/author_banner.jpg'})`}}>
          <div className='mainbreadcumb'>
          </div>
        </section>

        <section className='container no-bottom'>
          <div className='row'>
            <div className="col-md-12">
              <div className="d_profile de-flex">
                    <div className="de-flex-col">
                        <div className="profile_avatar">
                            <img src={profile.profile_pic ? profile.profile_pic : './img/author/author-7.jpg'} alt=""/>
                            <i className="fa fa-check"></i>
                            <div className="profile_name">
                                <h4>
                                    {profile.name}
                                    <span className="profile_username">@{Object.keys(profile).length > 0 ? profile.username : ''}</span>
                                    <span id="wallet" data-id={Object.keys(profile).length > 0 ? profile.hash : ''} className="profile_wallet">{Object.keys(profile).length > 0 ? formatWalletAddress(profile.hash) : ''}</span>
                                    <button id="btn_copy" title="Copy Text" onClick={copyToClipboard}>Copy</button>
                                </h4>
                            </div>
                        </div>
                    </div>
                    <div className="profile_follow de-flex">
                        <div className="de-flex-col">
                            <div className="profile_follower">500 followers</div>
                        </div>
                        <div className="de-flex-col">
                            <span className="btn-main">Follow</span>
                        </div>
                    </div>

                </div>
            </div>
          </div>
        </section>

        <section className='container no-top'>
          <div className='row'>
            <div className='col-lg-12'>
                <div className="items_filter">
                  <ul className="de_nav text-left">
                      <li id='Mainbtn' className="active"><span onClick={handleBtnClick}>On Sale</span></li>
                      <li id='Mainbtn1' className=""><span onClick={handleBtnClick1}>Created</span></li>
                      <li id='Mainbtn2' className=""><span onClick={handleBtnClick2}>Liked</span></li>
                  </ul>
              </div>
            </div>
          </div>
          {openMenu && (  
            <div id='zero1' className='onStep fadeIn'>
              <ColumnZero />
            </div>
          )}
          {openMenu1 && ( 
            <div id='zero2' className='onStep fadeIn'>
              <ColumnZeroTwo />
            </div>
          )}
          {openMenu2 && ( 
            <div id='zero3' className='onStep fadeIn'>
              <ColumnZeroThree />
            </div>
          )}
        </section>

        <Footer />
      </div>
    );
}
export default Colection;