import React, { Component } from "react";
import Clock from "../components/Clock";
import Footer from '../components/footer';
import { createGlobalStyle } from 'styled-components';
import axios from "axios";
import detectEthereumProvider from "@metamask/detect-provider";
import Web3 from "web3";
import Select from 'react-select'

const GlobalStyles = createGlobalStyle`
  header#myHeader.navbar.sticky.white {
    background: #403f83;
    border-bottom: solid 1px #403f83;
  }
  header#myHeader.navbar .search #quick_search{
    color: #fff;
    background: rgba(255, 255, 255, .1);
  }
  header#myHeader.navbar.white .btn, .navbar.white a, .navbar.sticky.white a{
    color: #fff;
  }
  header#myHeader .dropdown-toggle::after{
    color: rgba(255, 255, 255, .5);
  }
  header#myHeader .logo .d-block{
    display: none !important;
  }
  header#myHeader .logo .d-none{
    display: block !important;
  }
  @media only screen and (max-width: 1199px) {
    .navbar{
      background: #403f83;
    }
    .navbar .menu-line, .navbar .menu-line1, .navbar .menu-line2{
      background: #fff;
    }
    .item-dropdown .dropdown a{
      color: #fff !important;
    }
  }
`;

const customStyles = {
  option: (base, state) => ({
    ...base,
    background: "#fff",
    color: "#333",
    borderRadius: state.isFocused ? "0" : 0,
    "&:hover": {
      background: "#eee",
    }
  }),
  menu: base => ({
    ...base,
    borderRadius: 0,
    marginTop: 0
  }),
  menuList: base => ({
    ...base,
    padding: 0
  }),
  control: (base, state) => ({
    ...base,
    padding: 2
  })
};

const options = [
  { value: '', label: 'All categories' },
  { value: 'art', label: 'Art' },
  { value: 'music', label: 'Music' },
  { value: 'domain_names', label: 'Domain Names' },
  { value: 'virtual_world', label: 'Vitual World' },
  { value: 'trading_cards', label: 'Trading Cards' },
  { value: 'collectibles', label: 'Collectibles' },
  { value: 'sports', label: 'Sports' },
  { value: 'utility', label: 'Utility' }
]

export default class CreateNftItem extends Component {

constructor(props) {
    super(props);
    this.state = {
      message: '',
      submitted_success: false,
      files: [],
      type: 'fixed',
      category: '',
      price: '',
      title: '',
      description: '',
      minimum_bid: '',
      start_date: '',
      end_date: '',
      errors: {
        content: [],
        category: [],
        type: [],
        price: [],
        title: [],
        description: [],
        minimum_bid: [],
        start_date: [],
        end_date: [],
        account: []
      }
    };
  }

  onChange = (e) => {
    var files = e.target.files;
    document.getElementById("file_name").style.display = "none";
    this.setState({
      files: files
    })
  }

  handleShow = ()=>{
    this.setState({
      ...this.state,
      type: 'fixed'
    })
    document.getElementById("tab_opt_1").classList.add("show");
    document.getElementById("tab_opt_1").classList.remove("hide");
    document.getElementById("tab_opt_2").classList.remove("show");
    document.getElementById("btn1").classList.add("active");
    document.getElementById("btn2").classList.remove("active");
    document.getElementById("btn3").classList.remove("active");
  }
  handleShow1 = ()=>{
    this.setState({
      ...this.state,
      type: 'auction'
    })
    document.getElementById("tab_opt_1").classList.add("hide");
    document.getElementById("tab_opt_1").classList.remove("show");
    document.getElementById("tab_opt_2").classList.add("show");
    document.getElementById("btn1").classList.remove("active");
    document.getElementById("btn2").classList.add("active");
    document.getElementById("btn3").classList.remove("active");
  }

  handleShow2 = () => {
    this.setState({
      ...this.state,
      type: 'bid',
    })
    document.getElementById("tab_opt_1").classList.add("show");
    document.getElementById("tab_opt_2").classList.add("show");
    document.getElementById("btn1").classList.remove("active");
    document.getElementById("btn2").classList.remove("active");
    document.getElementById("btn3").classList.add("active");
  }

  state = {
     isActive:false  
  }
  unlockClick = () => {
    this.setState({
        isActive: true
    })
  }
  unlockHide = () => {
    this.setState({isActive: false});
  };

  onChangeInput = (e) => {
    this.setState({
      ...this.state,
      [e.target.name]: e.target.value
    })
  }

  showMessage = (message, time=5000) => {
    this.setState({
      ...this.state,
      message: message,
      submitted_success: true
    })

    setTimeout(() => {
      this.setState({
        ...this.state,
        submitted_success: false
      })
    }, 5000)
  }

  changeFileType = (e) => {
    if(e.value !== "") {
      this.setState({
        ...this.state,
        category: e.value,
        errors: {
          ...this.state.errors,
          category: []
        }
      })
    } else {
      this.setState({
        ...this.state,
        category: '',
        errors: {
          ...this.state.errors,
          category: ["Content type is required."]
        }
      })
    }
  }

  onSubmit = async (event) => {
    event.preventDefault();
    console.log('submitted.');
    const wallet = await detectEthereumProvider()
    if(wallet) {
      var web3 = new Web3(wallet)

      if(web3) {
        var accounts = await web3.eth.getAccounts();

        if(accounts.length > 0) {
          var formData = new FormData()
          formData.append('content', this.state.files[0], this.state.files[0].name);
          formData.append("type", this.state.type);
          formData.append("account", accounts[0]);
          formData.append("category", this.state.category);
          if(this.state.type === 'fixed') {
            
            formData.append("price", this.state.price);
            formData.append("description", this.state.description);
            formData.append("title", this.state.title);
          } else if(this.state.type === 'auction') {

            formData.append("minimum_bid", this.state.minimum_bid);
            formData.append("start_date", new Date(this.state.start_date));
            formData.append("end_date", new Date(this.state.end_date));
            formData.append("title", this.state.title);
            formData.append("description", this.state.description);
          } else if(this.state.type === 'bid') {

            formData.append("price", this.state.price);
            formData.append("minimum_bid", this.state.minimum_bid);
            formData.append("start_date", new Date(this.state.start_date));
            formData.append("end_date", new Date(this.state.end_date));
            formData.append("title", this.state.title);
            formData.append("description", this.state.description);
          }

          axios({
            method: 'POST',
            url: 'http://46.101.207.57/nft/',
            headers: {
              "Content-Type": "multipart/form-data"
            },
            data: formData
          }).then(response => {
            if(response.status === 201 && response.data.success) {
              this.setState({
                submitted_success: true,
                message: '',
                files: [],
                category: '',
                type: 'fixed',
                price: '',
                title: '',
                description: '',
                minimum_bid: '',
                start_date: '',
                end_date: '',
                errors: {
                  content: [],
                  category: [],
                  type: [],
                  content_type: [],
                  price: [],
                  title: [],
                  description: [],
                  minimum_bid: [],
                  start_date: [],
                  end_date: []
                }
              })

              this.showMessage("Nft item created successfully.");
            } else {
              console.log(response);
            }
          }).catch(e => {
            if(e.response.status === 403) {
              this.setState({
                ...this.state,
                errors: {
                  ...e.response.data.errors
                }
              })
            } else {
              console.log(e.response);
            }
          })
        } else {
          this.showMessage("please connect you crypto wallet.", 5000);
        }
      } else {
        this.showMessage("Please connect your crypto wallet.", 5000);
      }
    } else {
      this.showMessage("please install metamask wallet.", 5000);
    }

  }

render() {
    return (
      <div>
      <GlobalStyles/>

        <section className='jumbotron breadcumb no-bg' style={{backgroundImage: `url(${'./img/background/subheader.jpg'})`}}>
          <div className='mainbreadcumb'>
            <div className='container'>
              <div className='row m-10-hor'>
                <div className='col-12'>
                  <h1 className='text-center'>Create Nft Item</h1>
                </div>
              </div>
            </div>
          </div>
        </section>

        <section className='container'>

        <div className="row">
          <div className="col-lg-7 offset-lg-1 mb-5">
              <form id="form-create-item" className="form-border" action="#" method="post" onSubmit={this.onSubmit}>
              {this.state.submitted_success ? 
                (<div className="alert alert-success" role="alert">
                  {this.state.message}
                </div>) : (<p></p>)
              }
                  <div className="field-set">

                      <h5>NFT Content Type</h5>
                      <div className='dropdownSelect one'>
                        <Select required styles={customStyles} menuContainerStyle={{'zIndex': 999}} defaultValue={options[0]} options={options} onChange={this.changeFileType} />
                      </div>
                      <span>{this.state.errors.category && this.state.errors.category.length > 0 ? this.state.errors.category[0] : ''}</span>

                      <div className="spacer-single"></div>
                      
                      <h5>Upload Nft Image</h5>

                      <div className="d-create-file">
                        <p id="file_name">PNG, JPG, GIF, WEBP or MP4. Max 200mb.</p>
                        {Array.isArray(this.state.files) ?? this.state.files.map(x => 
                          <p key="{index}">PNG, JPG, GIF, WEBP or MP4. Max 200mb{x.name}</p>
                        )}
                        <div className='browse'>
                          <input type="button" id="get_file" className="btn-main" value="Browse"/>
                          <input id='upload_file' required  type="file" onChange={this.onChange} />
                        </div>
                        
                      </div>
                      <span>{this.state.errors.content && this.state.errors.content.length > 0 ? this.state.errors.content[0] : ''}</span>

                      <div className="spacer-single"></div>

                      <h5>Select method</h5>
                      <div className="de_tab tab_methods">
                        <ul className="de_nav">
                            <li id='btn1' className="active" onClick={this.handleShow}>
                              <span><i className="fa fa-tag"></i>Fixed price</span>
                            </li>
                            <li id='btn2' onClick={this.handleShow1}>
                              <span><i className="fa fa-hourglass-1"></i>Timed auction</span>
                            </li>
                            <li id='btn3' onClick={this.handleShow2}>
                              <span><i className="fa fa-users"></i>Open for bids</span>
                            </li>
                        </ul>
                        
                        <div className="de_tab_content pt-3">
                  
                          <div id="tab_opt_1">
                            <h5>Price</h5>
                            <input type="number" value={this.state.price}  step={0.01} inputMode="numeric" pattern="\d*"  name="price" id="price" className="form-control" placeholder="enter price for one item (ETH)" onChange={this.onChangeInput} />
                            <span>{this.state.errors.price && this.state.errors.price.length > 0 ? this.state.errors.price[0] : ''}</span>
                          </div>

                          <div id="tab_opt_2" className='hide'>
                              <h5>Minimum bid</h5>
                              <input type="number" value={this.state.minimum_bid} step={0.01} inputMode="numeric" pattern="\d*" name="minimum_bid" id="minimum_bid" className="form-control" placeholder="enter minimum bid" onChange={this.onChangeInput}/>
                              <span>{this.state.errors.minimum_bid && this.state.errors.minimum_bid.length > 0 ? this.state.errors.minimum_bid[0] : ''}</span>

                              <div className="spacer-20"></div>

                              <div className="row">
                                  <div className="col-md-6">
                                      <h5>Starting date</h5>
                                      <input type="date" value={this.state.start_date} name="start_date" id="start_date" className="form-control" min="1997-01-01" onChange={this.onChangeInput}/>
                                      <span>{this.state.errors.start_date && this.state.errors.start_date.length > 0 ? this.state.errors.start_date[0] : ''}</span>
                                  </div>
                                  <div className="col-md-6">
                                      <h5>Expiration date</h5>
                                      <input type="date" name="end_date" value={this.state.end_date} id="end_date" className="form-control" onChange={this.onChangeInput}/>
                                      <span>{this.state.errors.end_date && this.state.errors.end_date.length > 0 ? this.state.errors.end_date[0] : ''}</span>
                                  </div>
                              </div>
                          </div>
                          <div id="tab_opt_3"></div>
                        </div>

                      </div>

                      <div className="spacer-20"></div>

                      <h5>Title</h5>
                      <input type="text" required  name="title" value={this.state.title} id="title" className="form-control" placeholder="e.g. 'Crypto Funk" onChange={this.onChangeInput} />
                      <span>{this.state.errors.title && this.state.errors.title.length > 0 ? this.state.errors.title[0] : ''}</span>

                      <div className="spacer-10"></div>

                      <h5>Description</h5>
                      <textarea data-autoresize name="description" value={this.state.description} required  id="description" className="form-control" placeholder="e.g. 'This is very limited item'" onChange={this.onChangeInput}></textarea>
                      <span>{this.state.errors.description && this.state.errors.description.length > 0 ? this.state.errors.description[0] : ''}</span>

                      <div className="spacer-10"></div>

                      <button type="submit" id="submit" className="btn-main">Create Item</button>
                  </div>
              </form>
          </div>

          <div className="col-lg-3 col-sm-6 col-xs-12">
            <h5>Preview item</h5>
            <div className="nft__item m-0">
                <div className="de_countdown">
                  <Clock deadline="December, 30, 2021" />
                </div>
                <div className="author_list_pp">
                    <span>                                    
                        <img className="lazy" src="./img/author/author-1.jpg" alt=""/>
                        <i className="fa fa-check"></i>
                    </span>
                </div>
                <div className="nft__item_wrap">
                    <span>
                        <img src="./img/collections/coll-item-3.jpg" id="get_file_2" className="lazy nft__item_preview" alt=""/>
                    </span>
                </div>
                <div className="nft__item_info">
                    <span >
                        <h4>Pinky Ocean</h4>
                    </span>
                    <div className="nft__item_price">
                        0.08 ETH<span>1/20</span>
                    </div>
                    <div className="nft__item_action">
                        <span>Place a bid</span>
                    </div>
                    <div className="nft__item_like">
                        <i className="fa fa-heart"></i><span>50</span>
                    </div>                            
                </div> 
            </div>
          </div>                                         
      </div>

      </section>

        <Footer />
      </div>
   );
  }
}