import React, { useState } from "react";
import Footer from "../components/footer";
import { createGlobalStyle } from "styled-components";
import axios from "axios";
import { Redirect } from '@reach/router';

const GlobalStyles = createGlobalStyle`
  header#myHeader.navbar.sticky.white {
    background: #403f83;
    border-bottom: solid 1px #403f83;
  }
  header#myHeader.navbar .search #quick_search{
    color: #fff;
    background: rgba(255, 255, 255, .1);
  }
  header#myHeader.navbar.white .btn, .navbar.white a, .navbar.sticky.white a{
    color: #fff;
  }
  header#myHeader .dropdown-toggle::after{
    color: rgba(255, 255, 255, .5);
  }
  header#myHeader .logo .d-block{
    display: none !important;
  }
  header#myHeader .logo .d-none{
    display: block !important;
  }
  @media only screen and (max-width: 1199px) {
    .navbar{
      background: #403f83;
    }
    .navbar .menu-line, .navbar .menu-line1, .navbar .menu-line2{
      background: #fff;
    }
    .item-dropdown .dropdown a{
      color: #fff !important;
    }
  }
`;

const Register = ({auth, setAuth}) => {
  const [registerData, setRegisterData] = useState({
    username: "",
    email: "",
    phone: "",
    password: "",
    re_password: ""
  });

  const [errors, setErrors] = useState({
    name: [],
    username: [],
    email: [],
    phone: [],
    password: [],
    re_password: [],
    general: []
  })

  const onSubmit = (e) => {
    e.preventDefault();
    if(registerData.password !== registerData.re_password) {
      setErrors({
        ...errors,
        re_password: ["Password does not match."]
      })
    } else {
      axios({
        method: 'POST',
        url: 'http://46.101.207.57/users/register',
        headers: {
          "Content-Type": "application/json"
        },
        data: registerData
      }).then(response => {
        if(response.data.success) {
          setErrors({
            name: [],
            username: [],
            email: [],
            password: [],
            phone: [],
            re_password: []
          })

          if(response.data.body.token && response.data.body.token !== "") {
            localStorage.setItem('_token', response.data.body.token);

            setAuth(localStorage.getItem('_token') ? true : false);
          } else {
            setErrors({
              ...errors,
              general: ["Something went wrong."]
            })
          }
        }
      }).catch(e => {
        if(e.response.status === 401 || e.response.status === 403) {
          if(e.response.data.success === false && e.response.data.errors !== undefined) {
            setErrors({
              ...(e.response.data.errors),
              re_password: []
            })
          }
        }
      })
    }
  }

  const onChangeInput = (e) => {
    setRegisterData({
      ...registerData,
      [e.target.name]: e.target.value
    })
  }

  return (
    <div>
      {
        auth ? (<Redirect to='/home' noThrow/>) : (
          ''
        )
      }
      <GlobalStyles />

      <section
        className='jumbotron breadcumb no-bg'
        style={{ backgroundImage: `url(${"./img/background/subheader.jpg"})` }}>
        <div className='mainbreadcumb'>
          <div className='container'>
            <div className='row'>
              <div className='col-md-12 text-center'>
                <h1>Register</h1>
                <p>Anim pariatur cliche reprehenderit</p>
              </div>
            </div>
          </div>
        </div>
      </section>

      <section className='container'>
        <div className='row'>
          <div className='col-md-8 offset-md-2'>
            <h3>Don't have an account? Register now.</h3>
            <p>
              Sed ut perspiciatis unde omnis iste natus error sit voluptatem
              accusantium doloremque laudantium, totam rem aperiam, eaque ipsa
              quae ab illo inventore veritatis et quasi architecto beatae vitae
              dicta sunt explicabo.
            </p>

            <div className='spacer-10'></div>

            <form
              name='contactForm'
              id='contact_form'
              className='form-border'
              action='#' method="post" onSubmit={onSubmit}>
              <div className='row'>
                <div className='col-md-6'>
                  <div className='field-set'>
                    <label>Name:</label>
                    <input
                      type='text'
                      name='name'
                      id='name'
                      className='form-control'
                      value={registerData.name}
                      onChange={onChangeInput}
                    />
                    <span>{errors.name[0]}</span>
                  </div>
                </div>

                <div className='col-md-6'>
                  <div className='field-set'>
                    <label>Email Address:</label>
                    <input
                      type='text'
                      name='email'
                      id='email'
                      className='form-control'
                      value={registerData.email}
                      onChange={onChangeInput}
                    />
                    <span>{errors.email[0]}</span>
                  </div>
                </div>

                <div className='col-md-6'>
                  <div className='field-set'>
                    <label>Choose a Username:</label>
                    <input
                      type='text'
                      name='username'
                      id='username'
                      className='form-control'
                      value={registerData.username}
                      onChange={onChangeInput}
                    />
                    <span>{errors.username[0]}</span>
                  </div>
                </div>

                <div className='col-md-6'>
                  <div className='field-set'>
                    <label>Phone:</label>
                    <input
                      type='text'
                      name='phone'
                      id='phone'
                      className='form-control'
                      value={registerData.phone}
                      onChange={onChangeInput}
                    />
                    <span>{errors.phone[0]}</span>
                  </div>
                </div>

                <div className='col-md-6'>
                  <div className='field-set'>
                    <label>Password:</label>
                    <input
                      type='password'
                      name='password'
                      id='password'
                      className='form-control'
                      value={registerData.password}
                      onChange={onChangeInput}
                    />
                    <span>{errors.password[0]}</span>
                  </div>
                </div>

                <div className='col-md-6'>
                  <div className='field-set'>
                    <label>Re-enter Password:</label>
                    <input
                      type='password'
                      name='re_password'
                      id='re_password'
                      className='form-control'
                      value={registerData.re_password}
                      onChange={onChangeInput}
                    />
                    <span>{errors.re_password[0]}</span>
                  </div>
                </div>

                <div className='col-md-12'>
                  <div id='submit' className='pull-left'>
                    <input
                      type='submit'
                      id='send_message'
                      value='Register Now'
                      className='btn btn-main color-2'
                    />
                  </div>

                  <div className='clearfix'></div>
                </div>
              </div>
            </form>
          </div>
        </div>
      </section>

      <Footer />
    </div>
  );
};
export default Register;
